import React from 'react'

export default function Avatar (props) {
  function handleAvatarClick(e) {
  	if (typeof props.onAvatarClick === 'function'){
  		props.onAvatarClick()
  	}
  }


  const avatarType = props.type === 'circle' ? 'avatar-circle' : 'avatar-sqare'
  var avatarText = ''
  var name = null
  var imgUrl = props.user.avatar
    ? {backgroundImage: 'url(' + props.user.avatar + ')'}
    : {backgroundImage: "url('data:image/png;base64,')"}

  //if (props.type === 'square') {
  //  if ( props.user.from.name !== '' && props.user.from.name !== props.user.from.email ) {
  //    name = props.user.from.name.slice(0,1)
  //    imgUrl = {}
  //    avatarText = 'avatar-text'
  //  }
  //}

  return (
    <div className={`${avatarType} ${props.className} ${avatarText}`} style={imgUrl} onClick={(e)=>handleAvatarClick(props.isClickable)}>
      {name}
    </div>
  )
}
 


